    align-self: start;
  }
}
@media (max-width: 767px) {
  .footer__nav {
    grid-column: 1/-1;
    grid-row: 1/2;
    align-self: end;
    min-height: 137px;
  }
}
.footer__list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  max-width: 800px;
  margin-top: 3px;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 1023px) {
  .footer__list {
    gap: 30px;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .footer__list {
    flex-direction: column;
    flex-wrap: wrap;
    font-size: 16px;
    line-height: 19px;
    gap: 22px;
    max-height: 110px;
  }
  .footer__list li:first-of-type {
    margin-top: 41px;
  }
}
.footer__socials {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 11px;
  max-width: 100px;
}

